#highload1 {
  display: none;
}

#highload1 + .lb1, #highload1 + .lb1:before, #highload1 + .lb1:after {
  transition: all .3s;
}

#highload1 + .lb1 {
  width: 40px;
  height: 20px;
  cursor: pointer;
  border-radius: 30px;
  margin-top: 3px;
  margin-bottom: 2px;
  display: inline-block;
  position: relative;
}

#highload1 + .lb1:before {
  content: attr(data-offlabel);
  color: #000;
  font-family: Open Sans, sans-serif;
  font-size: 19px;
  display: block;
  position: absolute;
  top: 18px;
  right: 10px;
}

#highload1 + .lb1:after {
  content: "";
  width: 18px;
  height: 18px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  position: absolute;
  top: 1px;
  left: 1px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

#highload1:checked + .lb1:before {
  content: attr(data-onlabel);
  color: #fff;
  left: 16px;
  right: auto;
}

#highload1:checked + .lb1:after {
  background-color: #f7f7f7;
  left: 21px;
}

#highload1 + .lb1 {
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

#highload1:checked + .lb1 {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

#highload1:checked + .lb1:before {
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

